<template>
  <div class="home-container" ref="container" v-loading="isLoding">
    <ul
      class="carouitem"
      :style="{ marginTop }"
      @wheel="handleRoller"
      @transitionend="handleTransition"
    >
      <li v-for="item in banners" :key="item.id">
        <Carouseitem :date="item" />
      </li>
    </ul>
    <div class="icon">
      <i class="top" v-show="index >= 1" @click="switchTo(index - 1)">
        <Icon type="arrowUp" />
      </i>
      <i
        class="bottom"
        v-show="index < banners.length - 1"
        @click="switchTo(index + 1)"
      >
        <Icon type="arrowDown" />
      </i>
    </div>
    <ul class="indicate">
      <li
        @click="switchTo(i)"
        v-for="(item, i) in banners"
        :key="item.id"
        :class="{ active: i === index }"
      ></li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";
import Icon from "@/components/Icon.vue";
import Carouseitem from "./CarouseItem.vue";
export default {
  created() {
    this.$store.dispatch("banners/fetchBanner");
  },
  components: {
    Icon,
    Carouseitem,
  },
  data() {
    return {
      index: 0,
      containerHeight: 0,
      roller: false, //过渡效果是否结束
    };
  },

  mounted() {
    this.containerHeight = this.$refs.container.clientHeight;
    window.addEventListener("resize", this.handleResize);
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
  },
  computed: {
    ...mapState("banners", ["banners", "isLoding"]),
    marginTop() {
      return -this.index * this.containerHeight + "px ";
    },
  },
  methods: {
    switchTo(i) {
      this.index = i;
    },
    handleRoller(e) {
      if (this.roller) {
        return;
      }
      if (e.deltaY <= -125 && this.index > 0) {
        this.roller = true;
        this.index--; //往上翻
      } else if (e.deltaY >= 125 && this.index < this.banners.length - 1) {
        this.roller = true;
        this.index++; //往下翻
      }
    },
    handleTransition() {
      this.roller = false;
    },
    handleResize() {
      this.containerHeight = this.$refs.container.clientHeight;
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.home-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  ul {
    margin: 0;
    list-style: none;
    padding: 0;
    transition: 500ms;
  }
  .icon {
    font-size: 26px;
    color: @lightWords;
    cursor: pointer;
  }
  .top {
    position: absolute;
    top: 25px;
    left: 50%;
    transform: translate(-50%);
    animation: top 2s infinite;
  }
  .bottom {
    position: absolute;
    left: 50%;
    bottom: 25px;
    transform: translate(-50%);
    animation: bottom 2s infinite;
  }
}
@keyframes top {
  0% {
    transform: translateY(30%);
  }
  50% {
    transform: translateY(-30%);
  }
  100% {
    transform: translateY(30%);
  }
}
@keyframes bottom {
  0% {
    transform: translateY(-30%);
  }
  50% {
    transform: translateY(30%);
  }
  100% {
    transform: translateY(-30%);
  }
}
.indicate {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 16px;
  li {
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: black;
    margin-top: 16px;
    border: 1px solid #fff;
    cursor: pointer;
    &.active {
      background: #fff;
    }
  }
}
.carouitem {
  width: 100%;
  height: 100%;
  li {
    width: 100%;
    height: 100%;
  }
}
</style>
