<template>
  <div class="about-container" v-loading="loading || !srcLoading">
    <iframe
      @load="srcLoading = true"
      class="about-container"
      :src="src"
      frameborder="0"
    ></iframe>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      srcLoading: false,
    };
  },
  computed: mapState("about", { src: "banners", loading: "isLoding" }),
  created() {
    this.$store.dispatch("about/fetchAbout");
  },
};
</script>

<style scopend>
.about-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.about-container {
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
}
</style>
