// 入口文件
import "./mock"; //模拟数据入口文件
import Vue from "vue";
import App from "./App.vue";
import router from "./route";
import showMessage from "./utils/showMessage.js";
import directive from "@/directive"; //自定义指定配置导入
import ImgDirective from "@/directive/img.js";
import store from "@/store"; //导入仓库
store.dispatch("setting/fetchBanner"); //一开始就默认获取
Vue.directive("loading", directive);
Vue.directive("Img", ImgDirective);
Vue.prototype.$showMessage = showMessage;
import "./eventBus"; //事件总线
new Vue({
  store,
  router,
  render: (h) => h(App),
}).$mount("#app");
