//网站标题控制
let routeTitle = "",
  siteTitle = "";
function setTitle() {
  if (!routeTitle && !siteTitle) {
    document.title = "loading....";
  } else if (siteTitle && !routeTitle) {
    document.title = siteTitle;
  } else if (!siteTitle && routeTitle) {
    document.title = routeTitle;
  } else {
    document.title = `${routeTitle}-${siteTitle}`;
  }
}
export default {
  setRouteTitle(title) {
    routeTitle = title;
    setTitle();
  },
  setSiteTitle(title) {
    siteTitle = title;
    setTitle();
  },
};
