import axios from "axios";
import showMessage from "@/utils/showMessage";
//拦截器
const ins = axios.create(); //创建的实例
ins.interceptors.response.use(function(resp) {
  if (resp.data.code !== 0) {
    showMessage({
      content: resp.data.msg,
      type: "error",
    });
    console.log("没有数据");
    return;
  }
  return resp.data.data;
});
export default ins;
