<template>
  <div class="blog-comment-container">
    <messageArea
      title="评论列表"
      :subTitle="`(${banners.total})`"
      :isListLoading="isLoding"
      :list="banners.rows"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import messageArea from "@/components/MessageArea";
import fetchData from "@/minxins/fetchData.js";
import { getComment, submitComments } from "@/api/getBlog.js";
export default {
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  mixins: [fetchData({ total: 0, rows: [] })],
  components: {
    messageArea,
  },
  computed: {
    hasMore() {
      return this.banners.rows.length < this.banners.total;
    },
  },
  mounted() {
    this.$bus.$on("mainScroll", this.handleScroll);
  },
  //这两个事件总线的监控和取消监控
  destroyed() {
    this.$bus.$off("mainScroll", this.handleScroll);
  },
  methods: {
    handleScroll(dom) {
      if (this.isLoding || !dom) {
        return;
      }
      const errorValue = 80;
      const dec = Math.abs(dom.scrollTop + dom.clientHeight - dom.scrollHeight);
      if (dec <= errorValue) {
        this.loadMore();
      }
    },
    async fetch() {
      return await getComment(this.$route.params.id, this.page, this.limit);
    },
    async handleSubmit(user, fn) {
      const newObject = await submitComments({
        blogId: this.$route.params.id,
        ...user,
      });
      this.banners.rows.unshift(newObject);
      this.total++;
      fn("提交完成");
    },
    //加载更多消息
    async loadMore() {
      if (!this.hasMore) {
        return;
      }
      this.isLoding = true;
      this.page++;
      const reslut = await this.fetch();
      this.banners.total = reslut.total;
      this.banners.rows = this.banners.rows.concat(reslut.rows);
      this.isLoding = false;
    },
  },
};
</script>

<style scoped lang="less">
.blog-comment-container {
  margin: 30px 0;
}
</style>
