<template>
  <div class="blog-toc-container">
    <h2>目录</h2>
    <rightList :list="newArray" @Active="handleActive" />
  </div>
</template>
<script>
import rightList from "@/views/Blog/components/rightList.vue";
import RightList from "./rightList.vue";
export default {
  data() {
    return {
      anchor: "",
    };
  },
  mounted() {
    this.bounding = this.debounce(this.toActive, 50);
    this.$bus.$on("mainScroll", this.bounding);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.bounding);
  },
  computed: {
    //根据父组件传过来的数据返回一个新的数组
    newArray() {
      const arr = (toc = []) => {
        return toc.map((e) => ({
          ...e,
          active: this.anchor === e.anchor,
          children: arr(e.children),
        }));
      };
      return arr(this.toc);
    },
    //根据父组件传的toc拿到对应的html元素
    getDoms() {
      const doms = [];
      const dom = (toc) => {
        for (const e of toc) {
          doms.push(document.getElementById(e.anchor));
          if (e.children && e.children.length > 0) {
            dom(e.children);
          }
        }
      };
      dom(this.toc);
      return doms;
    },
  },
  components: {
    rightList,
    RightList,
  },
  props: {
    toc: {
      type: Array,
    },
  },
  methods: {
    handleActive(item) {
      location.hash = item.anchor;
    },
    //设置anchor为正确的数值
    toActive(Dom) {
      if (!Dom) {
        return;
      }
      this.anchor = "";
      const top = 200;
      for (const e of this.getDoms) {
        const domTop = e.getBoundingClientRect().top;
        if (domTop >= 0 && domTop <= top) {
          this.anchor = e.id;
          return;
        } else if (domTop > top) {
          return;
        } else {
          this.anchor = e.id;
        }
      }
    },
    //函数防抖
    debounce(fn, delay) {
      let timeId = null;
      return (...arg) => {
        clearTimeout(timeId);
        timeId = setTimeout(() => {
          fn(...arg);
        }, delay);
      };
    },
  },
};
</script>

<style scoped lang="less">
.blog-toc-container {
  h2 {
    text-align: center;
    font-weight: blod;
    letter-spacing: 2px;
    font-size: 1em;
  }
}
</style>
