import EventBus from "@/eventBus.js"; //事件总线
import url from "@/assets/default.gif";
/**
 * @param {新的函数} fn
 * @param {间隔事件} delay
 * @returns 返回一个新的函数
 */

function debounce(fn, delay) {
  let timeId = null;
  return (...arg) => {
    clearTimeout(timeId);
    timeId = setTimeout(() => {
      fn(...arg);
    }, delay);
  };
}
let imgs = []; //这里是正确的图片资源
function setImgage(img) {
  img.dom.src = url; //刚开始先使用默认图片
  const windowHeight = document.documentElement.clientHeight;
  const rect = img.dom.getBoundingClientRect(); //根据距离视口的距离来渲染图片
  const height = rect.height || 100;
  if (rect.top >= -height && rect.top <= windowHeight) {
    // const tempImg = new Image();
    // tempImg.onload = function() {
    //   img.dom.src = img.src;
    // };
    // tempImg.src = img.src;
    img.dom.src = img.src;
    imgs = imgs.filter((i) => i !== img);
  }
}
function setImgages() {
  for (const img of imgs) {
    setImgage(img);
  }
}

EventBus.$on("mainScroll", debounce(setImgages, 50));
export default {
  inserted(el, bindings) {
    const img = {
      dom: el,
      src: bindings.value,
    };
    imgs.push(img);
    setImgage(img);
  },
  unbind(el) {
    imgs = imgs.filter((img) => img.dom !== el);
  },
};
