<template>
  <div class="message-container" ref="main">
    <Message
      title="留言板"
      :subTitle="`(${banners.total})`"
      :isListLoading="isLoding"
      :list="banners.rows"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import Scroll from "@/minxins/scroll.js";
import * as api from "@/api/message.js";
import fetch from "@/minxins/fetchData.js";
import Message from "@/components/MessageArea/index.vue";
export default {
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  computed: {
    hasMore() {
      return this.banners.rows.length < this.banners.total;
    },
  },
  mounted() {
    this.$bus.$on("mainScroll", this.handleScroll);
  },
  //这两个事件总线的监控和取消监控
  destroyed() {
    this.$bus.$off("mainScroll", this.handleScroll);
  },
  mixins: [fetch({ total: 0, rows: [] }), Scroll("main")],
  methods: {
    async fetch() {
      return await api.getMessage(this.page, this.limit);
    },
    async handleSubmit(user, fn) {
      const newObject = await api.postMessage(user);
      fn("提交完成");
      this.banners.rows.unshift(newObject);
    },
    handleScroll(dom) {
      if (this.isLoding || !dom) {
        return;
      }
      const errorValue = 80;
      const dec = Math.abs(dom.scrollTop + dom.clientHeight - dom.scrollHeight);
      if (dec <= errorValue) {
        this.loadMore();
      }
    },
    async loadMore() {
      if (!this.hasMore) {
        return;
      }
      this.isLoding = true;
      this.page++;
      const reslut = await this.fetch();
      this.banners.total = reslut.total;
      this.banners.rows = this.banners.rows.concat(reslut.rows);
      this.isLoding = false;
    },
  },
  components: {
    Message,
  },
};
</script>

<style scoped>
.message-container {
  width: 100%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
  overflow-y: auto;
  position: relative;
  scroll-behavior: smooth;
}
.message-area-container {
  width: 800px;
  margin: 0 auto;
}
</style>
