<template>
  <div class="Page-container" v-if="pageCount >= 1">
    <a @click="pageChange(1)" :class="{ dis: current === 1 }">|&lt;&lt;</a>
    <a @click="pageChange(current - 1)" :class="{ dis: current === 1 }"
      >|&lt;&lt;</a
    >
    <a
      @click="pageChange(n)"
      v-for="(n, i) in displayLength"
      :key="i"
      :class="{ active: current === n }"
      >{{ n }}</a
    >
    <a @click="pageChange(current + 1)" :class="{ dis: current === maxPage }"
      >&gt;&gt;|</a
    >
    <a @click="pageChange(pageCount)" :class="{ dis: current === maxPage }"
      >&gt;&gt;|</a
    >
  </div>
</template>

<script>
export default {
  props: {
    current: {
      type: Number,
      default: 1,
      required: false,
    },
    //总数据量
    total: {
      type: Number,
      default: 0,
      required: false,
    },
    //页容量
    limit: {
      type: Number,
      default: 10,
      required: false,
    },
    //可见页码
    visibleNumber: {
      type: Number,
      default: 10,
      required: false,
    },
  },
  computed: {
    //总页数
    pageCount() {
      return Math.floor(this.total / this.limit);
    },
    //最小页数
    minPage() {
      let inP = this.current - Math.floor(this.visibleNumber / 2);
      if (inP < 1) {
        inP = 1;
      }
      return inP;
    },
    //最大页数
    maxPage() {
      let axP = this.minPage + this.visibleNumber - 1;
      if (axP > this.pageCount) {
        axP = this.pageCount;
      }
      return axP;
    },
    //显示页码范围
    displayLength() {
      const visible = [];
      for (let i = this.minPage; i <= this.maxPage; i++) {
        visible.push(i);
      }
      return visible;
    },
  },
  methods: {
    pageChange(newPage) {
      if (newPage < 1) {
        newPage = 1;
      }
      if (newPage > this.pageCount) {
        newPage = this.pageCount;
      }
      if (newPage === this.current) {
        return;
      }
      this.$emit("pageChange", newPage);
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/global.less";
@import "~@/styles/var.less";
.Page-container {
  display: flex;
  justify-content: center;
  margin: 20px 0;
  a {
    margin-left: 8px;
    color: @primary;
    cursor: pointer;
    &.active {
      color: @dark;
      font-weight: bold;
    }
    &.dis {
      color: @gray;
      cursor: not-allowed;
    }
  }
}
</style>
