<template>
  <div @click="handleClick" class="top-container" v-if="show">top</div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  created() {
    this.$bus.$on("mainScroll", this.handlScroll);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.handlScroll);
  },
  methods: {
    handlScroll(dom) {
      if (!dom) {
        this.show = false;
      } else {
        this.show = dom.scrollTop >= 500;
      }
    },
    handleClick() {
      this.$bus.$emit("setClick", 0);
    },
  },
};
</script>

<style scoped lang="less">
.top-container {
  position: fixed;
  color: hsl(0, 0%, 100%);
  bottom: 20px;
  right: 20px;
  z-index: 666;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  line-height: 50px;
  text-align: center;
  background: #6b9eee;
  cursor: pointer;
}
</style>
