import Mock from "mockjs";
const queryString = require("querystring");
// 提交评论
Mock.mock("/api/message", "post", {
  code: 0,
  msg: "",
  data: {
    id: "@guid",
    nickname: "@cname",
    content: "@cparagraph(1, 10)",
    createDate: "@date('yyyy-MM-dd')",
    "avatar|1": [
      "https://img0.baidu.com/it/u=171752860,2444777523&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1692550800&t=7cd71a4d9870d3de3d61f2ef328bccf9",
      "https://t13.baidu.com/it/u=1407605080,3504409894&fm=224&app=112&size=w931&n=0&f=JPEG&fmt=auto?sec=1692550800&t=11f45bf291ed73481b484735b6fd2bd3",
      "https://img1.baidu.com/it/u=1508990132,3643620231&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1692550800&t=9396ed712ae9fe41262bad5fb83c3ba0",
      "https://t13.baidu.com/it/u=4142574906,1473271580&fm=224&app=112&size=w931&n=0&f=JPEG&fmt=auto?sec=1692550800&t=94591d5d1dede73cc8a7be2a758d235b",
    ],
  },
});
//分页获取评论
Mock.mock(/^\/api\/message\/?(\?.+)?$/, "get", function(options) {
  const query = queryString.parse(options.url);
  return Mock.mock({
    code: 0,
    msg: "",
    data: {
      "total|40": 0,
      [`rows|${query.limit || 10}`]: [
        {
          id: "@guid",
          nickname: "@cname",
          content: "@cparagraph(1, 10)",
          createDate: "@date('yyyy-MM-dd')",
          "avatar|1": [
            "https://img0.baidu.com/it/u=171752860,2444777523&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1692550800&t=7cd71a4d9870d3de3d61f2ef328bccf9",
            "https://t13.baidu.com/it/u=1407605080,3504409894&fm=224&app=112&size=w931&n=0&f=JPEG&fmt=auto?sec=1692550800&t=11f45bf291ed73481b484735b6fd2bd3",
            "https://img1.baidu.com/it/u=1508990132,3643620231&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1692550800&t=9396ed712ae9fe41262bad5fb83c3ba0",
            "https://t13.baidu.com/it/u=4142574906,1473271580&fm=224&app=112&size=w931&n=0&f=JPEG&fmt=auto?sec=1692550800&t=94591d5d1dede73cc8a7be2a758d235b",
          ],
        },
      ],
    },
  });
});
