import Vue from "vue";
import VueRouter from "vue-router";
if (!window.VueRouter) {
  Vue.use(VueRouter);
}
Vue.use(VueRouter);
import peiZhi from "./peiZhi.js";
import setTitle from "@/utils/setTitle.js";
const router = new VueRouter({
  routes: peiZhi,
  mode: "history",
});
router.afterEach((to, from) => {
  if (to.meta.title) {
    setTitle.setRouteTitle(to.meta.title);
  }
});
export default router;
