<template>
  <div
    class="carouseitem-container"
    ref="container"
    @mousemove="handleMouseMove"
    @mouseleave="handleMouseLeave"
  >
    <div class="img-container" ref="image" :style="imagePosition">
      <ImageLoader
        :src="date.bigImg"
        :placeholder="date.midImg"
        @load="showWords"
      />
    </div>

    <p class="title" ref="title">{{ date.title }}</p>
    <p class="description" ref="description">{{ date.description }}</p>
  </div>
</template>

<script>
import ImageLoader from "@/components/ImageLoader/background.vue";
export default {
  props: ["date"],
  components: {
    ImageLoader,
  },
  data() {
    return {
      titleWidth: 0,
      descriptionWidth: 0,
      containerSize: null, //外层容器尺寸
      imageSize: null, //里层图片尺寸
      mouseX: 0, //鼠标横坐标
      mouseY: 0, //鼠标纵坐标
    };
  },
  mounted() {
    (this.titleWidth = this.$refs.title.clientWidth),
      (this.descriptionWidth = this.$refs.description.clientWidth);
    this.setSize(); //设置两个容器尺寸
    this.mouseX = this.center.x;
    this.mouseY = this.center.y;
    //当窗口大小尺寸发生变化时及时调整窗口大小
    window.addEventListener("resize", this.setSize);
  },
  destroyed() {
    window.removeEventListener("resize", this.setSize);
  },
  computed: {
    //得到图片位置
    imagePosition() {
      if (!this.imageSize || !this.containerSize) {
        return;
      }
      const MaxWidth = this.imageSize.width - this.containerSize.width;
      const MaxHeight = this.imageSize.height - this.containerSize.height;

      const X = (-MaxWidth / this.containerSize.width) * this.mouseX;
      const Y = (-MaxHeight / this.containerSize.height) * this.mouseY;

      return { transform: `translate(${X}px,${Y}px)` };
    },
    //默认图片是居中显示的
    center() {
      return {
        x: this.containerSize.width / 2,
        y: this.containerSize.height / 2,
      };
    },
  },
  methods: {
    //调用方法展示文字
    showWords() {
      this.$refs.title.style.opacity = 1;
      this.$refs.title.style.width = 0;
      this.$refs.title.clientWidth;
      this.$refs.title.style.transition = "1s";
      this.$refs.title.style.width = this.titleWidth + "px";
      this.$refs.description.style.opacity = 1;
      this.$refs.description.style.width = 0;
      this.$refs.description.clientWidth;
      this.$refs.description.style.transition = "2s 1s"; //等待1s后再执行
      this.$refs.description.style.width = this.descriptionWidth + "px";
    },
    setSize() {
      this.containerSize = {
        width: this.$refs.container.clientWidth,
        height: this.$refs.container.clientHeight,
      };
      this.imageSize = {
        width: this.$refs.image.clientWidth,
        height: this.$refs.image.clientHeight,
      };
    },
    //记录鼠标距离外层容器的坐标
    handleMouseMove(e) {
      const rect = this.$refs.container.getBoundingClientRect();
      this.mouseX = e.clientX - rect.left;
      this.mouseY = e.clientY - rect.top;
    },
    handleMouseLeave() {
      this.mouseX = this.center.x;
      this.mouseY = this.center.y;
    },
  },
};
</script>

<style scoped lang="less">
.carouseitem-container {
  white-space: nowrap;
  width: 100%;
  height: 100%;
  color: #fff;
  position: relative;
  overflow: hidden;
}
.img-container {
  width: 110%;
  height: 110%;
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.3s;
}
.title,
.description {
  left: 25px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  letter-spacing: 5px;
  overflow: hidden;
  opacity: 0;
}
.title {
  font-size: 26px;
}
.description {
  font-size: 18px;
  top: 60%;
}
</style>