<template>
  <form class="data-from" @submit.prevent="handleClick">
    <div class="userId">
      <input
        type="text"
        placeholder="用户昵称"
        maxlength="10"
        v-model="user.nickname"
      />
      <p class="top">{{ user.nickname.length }}/10</p>
    </div>
    <div class="err">{{ error.nickname }}</div>
    <div class="textContent">
      <textarea
        type="text"
        placeholder="输入内容"
        v-model="user.content"
        maxlength="300"
      />
      <p class="buttom">{{ user.content.length }}/300</p>
    </div>
    <div class="err">{{ error.content }}</div>
    <div class="but">
      <button :disabled="isSubmit">{{ isSubmit ? "正在提交" : "提交" }}</button>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      user: {
        nickname: "",
        content: "",
      },
      error: {
        nickname: "",
        content: "",
      },
      isSubmit: false,
    };
  },
  methods: {
    handleClick() {
      this.error.nickname = this.user.nickname ? "" : "请输入昵称";
      this.error.content = this.user.content ? "" : "请输入内容";
      if (this.error.nickname || this.error.content) {
        return; //任何一个内容区域内是没有数据的都不能提交
      }
      this.isSubmit = true;
      this.$emit("submit", this.user, (result) => {
        (this.user.nickname = ""),
          (this.user.content = ""),
          (this.isSubmit = false);
        this.$showMessage({
          content: result,
          type: "success",
          duration: 1500,
        });
      });
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.err {
  margin: 12px;
  color: @danger;
  font-size: 14px;
  height: 20px;
  line-height: 20px;
}
.userId {
  width: 50%;
  position: relative;
  .top {
    font-size: 6px;
    color: @gray;
    position: absolute;
    left: 91.5%;
    top: 12px;
  }
}
.textContent {
  position: relative;
  .buttom {
    font-size: 6px;
    color: @gray;
    position: absolute;
    right: 10px;
    top: 90px;
  }
}
input,
textarea {
  display: block;
  width: 100%;
  box-sizing: border-box;
  border: 1px dashed @gray;
  outline: none;
  color: @words;
  font-size: 14px;
  border-radius: 4px;
  &:focus {
    border-color: @primary;
  }
}
input {
  padding: 0 15px;
  height: 40px;
}
textarea {
  resize: none;
  height: 120px;
  padding: 10px;
}
button {
  position: relative;
  cursor: pointer;
  border: none;
  outline: none;
  width: 100px;
  height: 34px;
  color: #fff;
  border-radius: 4px;
  background: @primary;
  &:hover {
    background: darken(@primary, 10%);
  }
  &:disabled {
    background: lighten(@primary, 20%);
    cursor: not-allowed;
  }
}
</style>