import { getGlobleSett } from "@/api/getBlog";
import setTitle from "@/utils/setTitle";
export default {
  namespaced: true,
  state: {
    isLoding: false,
    banners: [],
  },
  mutations: {
    setLoading(state, payload) {
      state.isLoding = payload;
    },
    setData(state, payload) {
      state.banners = payload;
    },
  },
  actions: {
    async fetchBanner(ctx) {
      if (ctx.state.banners.length) {
        return;
      }
      ctx.commit("setLoading", true);
      const resp = await getGlobleSett();
      ctx.commit("setData", resp);
      ctx.commit("setLoading", false);
      //首先判断数据是否加载完成，然后设置导航栏小图标
      if (resp.favicon) {
        let link = document.querySelector("link[ref='shortcut icon']");
        if (link) {
          return;
        }
        link = document.createElement("link");
        link.rel = "shortcut icon";
        link.type = "images/x-icon";
        link.href = resp.favicon;
        document.querySelector("head").appendChild(link);
      }
      if (resp.siteTitle) {
        setTitle.setSiteTitle(resp.siteTitle);
      }
    },
  },
};
