<template>
  <div class="background-contaienr">
    <img :src="placeholder" v-if="!loding" />
    <img
      class="head"
      :src="src"
      @load="load"
      :style="{ opacity: islod, transition: `${duration}ms` }"
    />
  </div>
</template>

<script>
export default {
  props: {
    src: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      required: true,
    },
    duration: {
      type: Number,
      default: 500,
    },
  },
  data() {
    return {
      isloding: false,
      loding: false,
    };
  },
  computed: {
    islod() {
      return this.isloding ? 1 : 0;
    },
  },
  methods: {
    load() {
      this.isloding = true;
      setTimeout(() => {
        this.loding = true;
        this.$emit("load");
      }, this.duration);
    },
  },
};
</script>

<style scoped lang="less">
.background-contaienr {
  width: 100%;
  height: 100%;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    object-fit: cover;
  }
  .head {
    opacity: 0;
  }
}
</style>