import { Store, install } from "vuex";
import Vue from "vue";
import banners from "./banner";
import setting from "./setting";
import about from "./about";
import demo from "./demo";
if (!window.Vuex) {
  install(Vue);
}

export default new Store({
  modules: {
    banners,
    setting,
    about,
    demo,
  },
  strict: true,
});
