<template>
  <ul class="rightList-container">
    <li v-for="(items, i) in list" :key="i">
      <span @click="handleClick(items)" :class="{ active: items.active }">{{
        items.name
      }}</span>
      <span
        class="dis"
        @click="handleClick(items)"
        v-if="items.dis"
        :class="{ active: items.active }"
        >{{ items.dis }}</span
      >
      <!-- 组件递归（使用自身组件抛出的事件，并且再一次抛出） -->
      <rightList :list="items.children" @Active="handleClick" />
    </li>
  </ul>
</template>
<script>
export default {
  name: "rightList",
  props: {
    list: { type: Array, default: () => [] },
  },
  methods: {
    handleClick(item) {
      if (!item.active) {
        this.$emit("Active", item);
      }
    },
  },
};
</script>

<style scoped lang="less">
.rightList-container {
  padding: 0;
  list-style: none;

  li {
    cursor: pointer;
    min-height: 35px;
    margin-top: 6px;
    margin-left: 16px;
    line-height: 35px;
    font-weight: lighter;
    // margin-left: 1.5em;
    font-size: 14px;
    .active {
      color: #dc6a12;
    }
  }
}
.dis {
  font-size: 12px;
  margin-left: 10px;
  color: #b4b8bc;
}
</style>