<template>
  <div class="BogList-contaienr" v-loading="isLoding" ref="container">
    <ul>
      <li v-for="(items, i) in banners.rows" :key="i">
        <div class="img" v-if="items.thumb">
          <router-link
            :to="{
              name: 'bolgDetails',
              params: { id: items.id },
            }"
            ><img v-Img="items.thumb" :alt="items.title" :title="items.title"
          /></router-link>
        </div>
        <div class="main">
          <router-link
            :to="{
              name: 'bolgDetails',
              params: { id: items.id },
            }"
            ><h2>{{ items.title }}</h2></router-link
          >
          <div class="date">
            <span>日期：{{ items.createDate }}</span>
            <span>浏览：{{ items.commentNumber }}</span>
            <span>评论{{ items.scanNumber }}</span>
            <span>{{ items.category.name }}</span>
          </div>
          <div class="describe">{{ items.description }}</div>
        </div>
      </li>
    </ul>
    <page
      v-if="banners.total"
      @pageChange="handlePageChange"
      :total="banners.total"
      :limit="routeInfo.limit"
      :current="routeInfo.page"
      :visibleNumber="10"
    />
  </div>
</template>

<script>
import { getBlog } from "@/api/getBlog.js";
import page from "@/components/Page.vue";
import fetchDate from "@/minxins/fetchData.js";
import Scroll from "@/minxins/scroll.js";
export default {
  components: {
    page,
  },
  computed: {
    routeInfo() {
      const page = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 10;
      const category = +this.$route.params.CategoryId || -1;
      return {
        page,
        limit,
        category,
      };
    },
  },
  // mounted() {
  //   this.$refs.container.addEventListener("scroll", this.scroll);
  //   this.$bus.$on("setClick", this.handleClick);
  // },
  beforeDestroy() {
    this.$bus.$off("setClick", this.handleClick);
    this.$refs.container.removeEventListener("scroll", this.scroll);
  },
  mixins: [fetchDate({}), Scroll("container")],
  methods: {
    async fetch() {
      return await getBlog(
        this.routeInfo.page,
        this.routeInfo.limit,
        this.routeInfo.category
      );
    },
    handlePageChange(newPage) {
      const query = {
        page: newPage,
        limit: this.routeInfo.limit,
      };
      //没有分页的情况
      if (this.routeInfo.category === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        //这是有分页的情况
        this.$router.push({
          name: "Category",
          query,
          params: {
            CategoryId: this.routeInfo.category,
          },
        });
      }
      // 跳转到当前的分类
    },
    // scroll() {
    //   this.$bus.$emit("mainScroll", this.$refs.container);
    // },
    // handleClick(num) {
    //   this.$refs.container.scrollTop = num;
    // },
  },
  // 监视$toute内的相关数据发生变化后运行
  watch: {
    async $route() {
      this.isLoding = true;
      //滚动条上升
      this.$refs.container.scrollTop = 0;
      this.banners = await this.fetch();
      this.isLoding = false;
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.BogList-contaienr {
  // line-height: 1.7;
  position: relative;
  padding: 20px;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth; // 每次加载完成数据后滚动条平滑滚动到0
  a {
    text-decoration: none;
    color: inherit;
    // white-space: normal;
  }
  a:hover {
    color: @primary;
  }
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}
li {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid @gray;
  .img {
    padding: 8px;
    margin-right: 15px;
    img {
      display: block;
      max-width: 200px;
      border-radius: 5px;
    }
  }
  .main {
    flex: 1 1 auto;
    h2 {
      margin: 0;
    }
  }
  .date {
    font-size: 12px;
    color: @gray;
    span {
      margin-right: 15px;
    }
  }
  .describe {
    white-space: normal;
    margin: 15px 0;
    font-size: 14px;
    cursor: pointer;
  }
}
</style>
