<template>
  <Layout>
    <template>
      <div ref="main" class="main-container" v-loading="isLoding">
        <BlogDetail :blog="banners" v-if="banners" />
        <BlogComment v-if="banners" />
      </div>
    </template>
    <template #right>
      <div class="toc-container" v-loading="isLoding">
        <BlogToc :toc="banners.toc" v-if="banners" />
      </div>
    </template>
  </Layout>
</template>

<script>
import setTitle from "@/utils/setTitle.js";
import Layout from "@/components/Layout";
import BlogComment from "./components/BlogComment.vue";
import BlogToc from "./components/BlogToc.vue";
import BlogDetail from "./components/BlogDetail.vue";
import fetchData from "@/minxins/fetchData.js";
import { getBlogs } from "@/api/getBlog.js";
import mainscroll from "@/minxins/scroll.js";
export default {
  mixins: [fetchData(null), mainscroll("main")],
  components: {
    BlogDetail,
    Layout,
    BlogToc,
    BlogComment,
  },
  methods: {
    async fetch() {
      const resp = await getBlogs();
      setTitle.setRouteTitle(resp.title);
      return resp;
    },
  },
  //当页面数据变化，刷新时定位到刷新前的页面
  updated() {
    const hash = location.hash;
    location.hash = "";
    setTimeout(() => {
      location.hash = hash;
    }, 50);
  },
};
</script>
<style scoped lang="less">
.toc-container {
  overflow-y: auto;
  box-sizing: border-box;
  box-sizing: border-box;
  width: 260px;
  height: 100%;
  position: relative;
  padding: 20px;
}

.main-container {
  overflow-x: hidden;
  box-sizing: border-box;
  height: 100%;
  overflow-y: scroll;
  padding: 20px;
  position: relative;
  scroll-behavior: smooth;
}
</style>
