<template>
  <div class="BlogCategory-container" v-loading="isLoding">
    <h2>文章分类</h2>
    <rightList :list="list" v-if="list" @Active="handleActive" />
  </div>
</template>
<script>
import rightList from "@/views/Blog/components/rightList.vue";
import { getBlogCategories } from "@/api/getBlog.js";
import fetchDate from "@/minxins/fetchData.js";
export default {
  mixins: [fetchDate([])],
  components: {
    rightList,
  },
  methods: {
    async fetch() {
      return await getBlogCategories();
    },
    handleActive(item) {
      const query = {
        page: 1,
        limit: this.limit,
      };
      if (item.id === -1) {
        this.$router.push({
          name: "Blog",
          query,
        });
      } else {
        this.$router.push({
          name: "Category",
          query,
          params: {
            CategoryId: item.id,
          },
        });
      }
    },
  },
  computed: {
    categoryId() {
      return +this.$route.params.CategoryId || -1;
    },
    limit() {
      return +this.$route.query.limit || 10;
    },
    list() {
      const articleCount = this.banners.reduce((a, b) => a + b.articleCount, 0);
      const result = [{ name: "全部", id: -1, articleCount }, ...this.banners];
      return result.map((it) => ({
        ...it, //把对象里的每一个都展开
        active: it.id === this.categoryId,
        dis: `${it.articleCount}篇`,
      }));
    },
  },
};
</script>

<style scoped lang='less'>
.BlogCategory-container {
  overflow-y: auto;
  box-sizing: border-box;
  box-sizing: border-box;
  width: 260px;
  height: 100%;
  position: relative;
  padding: 20px;
  h2 {
    text-align: center;
    font-weight: blod;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>